import React from 'react';
import PropTypes from 'prop-types';

import { Row, Col, Container } from 'react-bootstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { getIconForHardSkill } from '../../mock/aboutMe/hardSkillsData';

import '../../style/pages/_hardSkills.scss';

const generateUniqueKey = () => {
  return Math.random().toString(36).substring(7);
};

const HardSkills = ({ hardSkills }) => {
  const firstCategory = Object.keys(hardSkills)[0];
  const otherCategories = Object.keys(hardSkills).slice(1, 5);

  return (
    <Container fluid className="hardskills-page-container">
      <Row className="hardskills-row">
        <Col lg={4} className="hardskill-column">
          <div className="skill-card large-category">
            <h2 className="hardskills-category-heading">{firstCategory}</h2>
            {hardSkills[firstCategory].map((hardskill) => (
              <div className="hardskill-item" key={generateUniqueKey()}>
                <FontAwesomeIcon icon={getIconForHardSkill(hardskill)} className="icon" />
                <span className="skill-text">{hardskill}</span>
              </div>
            ))}
          </div>
        </Col>

        <Col lg={8} className="other-categories">
          <Row className="h-100">
            {otherCategories.map((category) => (
              <Col xs={6} className="hardskill-column small-category" key={category}>
                <div className="skill-card">
                  <h2 className="hardskills-category-heading">{category}</h2>
                  {hardSkills[category].map((hardskill) => (
                    <div className="hardskill-item" key={generateUniqueKey()}>
                      <FontAwesomeIcon icon={getIconForHardSkill(hardskill)} className="icon" />
                      <span className="skill-text">{hardskill}</span>
                    </div>
                  ))}
                </div>
              </Col>
            ))}
          </Row>
        </Col>
      </Row>
    </Container>
  );
};

HardSkills.propTypes = {
  hardSkills: PropTypes.objectOf(PropTypes.arrayOf(PropTypes.string)).isRequired,
};

export default HardSkills;
